.App {
  text-align: center;
  font-family: 'Glober Bold';
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 100vw;
  color: white;
}

.App-header h1{
  font-size: 8%; 
}

.App-header h1 span.colorPlx{
  color:#1DA1F2;
}

.App-header h1 span:hover{
  position: relative;
  top: -10px;
}

@media (max-width: 1000px){
  .App-header h1{
    font-size: 15%;
  }
}